// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Onest", sans-serif;
  --second-family: "Unbounded", sans-serif;
  --third-family: "Inter", sans-serif;

  --content-width: 1160px;
  --content-fluid-width: 1790px;

  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(
    var(--content-fluid-width) + (var(--container-offset) * 2)
  );

  // colors
  --light-color: #fff;
  --dark-color: #010202;
  --blue-color: #0281be;
  --accent-color: #e34646;
  --block-color: linear-gradient(173deg, #0281be 0%, #013c58 100%);
}
