.step1 {
  background-color: var(--dark-color);
  padding-top: 70px;
  padding-bottom: 184px;

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 36px;
    text-transform: uppercase;
    color: var(--accent-color);
    margin-bottom: 55px;
  }

  &__content {
    border-radius: 22px;
    background-color: var(--blue-color);
    padding: 35px 58px;
  }

  &__btn {
    width: 90%;
  }

  &__order {
    padding: 21px 47px;
    background-color: var(--light-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 16px;
    margin-bottom: 56px;
  }

  &__info {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 100px;
    align-items: center;
    margin-bottom: 56px;
  }

  &__phone {
    border-radius: 16px;
    background-color: var(--light-color);
    padding: 12px 17px;

    & p {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 24px;
      text-transform: lowercase;
      color: var(--dark-color);

      & span {
        color: var(--blue-color);
      }
    }
  }

  &__calc {
    border-radius: 16px;
    background-color: var(--light-color);
    padding: 12px 17px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    text-transform: lowercase;
    color: var(--dark-color);
    display: flex;
    justify-content: space-between;
  }

  &__info--price {
    display: flex;
    align-items: center;

    & span {
      margin-left: auto;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 32px;
      text-transform: lowercase;
      text-align: right;
      color: var(--light-color);
      margin-right: 24px;
    }
  }

  &__footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 360px;
  }
  &__footer--left {
    display: flex;
    flex-direction: column;
  }

  &__footer--course {
    margin: 0;
    margin-bottom: 18px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    color: var(--light-color);
    &-logo {
      border-radius: 16px;
      background-color: var(--light-color);
      padding: 56px 115px;
    }
  }

  &__footer--right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__footer--price {
    margin-top: 38px;
    display: flex;
    justify-content: space-between;
    align-items: end;

    &-text {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 24px;
      color: #fff;
    }
    &-money {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 32px;

      color: #fff;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    & button {
      width: 90%;
      cursor: pointer;
    }
  }
}
