.advantages {
  background-color: var(--dark-color);
  padding-top: 39px;
  padding-bottom: 120px;

  &__container {
    border-radius: 22px;
    background-color: #f5f5f5;
    padding: 46px 100px 93px 100px;
    position: relative;
  }

  &__raketa {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 32px;
    text-transform: uppercase;
    color: var(--dark-color);
    margin: 0;
    margin-bottom: 35px;

    & span {
      color: var(--accent-color);
    }
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }

  &__item {
    position: relative;
    border-radius: 22px;
    background-color: var(--light-color);
    padding: 15px 45px;
    &--raketa {
      position: relative;
      background-color: initial;
    }
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    color: var(--dark-color);
  }

  &__icon {
    position: absolute;
    right: 20px;
  }
}
