.ps {
  background-color: var(--dark-color);
  &__container {
    padding-bottom: 127px;
  }
  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 36px;
    text-align: center;
    color: #fff;
    margin-bottom: 21px;
  }
  &__blocks {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;

    &--1 {
      padding: 44px 47px;
      border-radius: 22px;
      background-color: var(--blue-color);
    }

    &--big-text {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 24px;
      text-transform: uppercase;
      color: #fff;
    }
    &--2 {
      padding: 44px 47px;
      border-radius: 22px;
      border: 1px solid #b5b5b5;
    }

    &--3 {
      justify-content: end;

      & .btn-red {
        margin-right: 22px;
      }
    }

    &--4 {
      position: relative;
      padding-left: 70px;

      &::before {
        content: "";
        position: absolute;
        width: 73px;
        height: 73px;
        background-image: url("../img/arrow.svg");
        background-position: center center;
        background-repeat: no-repeat;
        left: 0;
      }
    }
  }

  &__litle-text {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;

    color: #e3e3e3;
  }
}
