.step2 {
  background-color: var(--dark-color);
  padding-top: 70px;
  padding-bottom: 184px;
  position: relative;

  &__cosmos {
    position: absolute;
    width: 390px;
    height: 350px;
    top: -50px;
    right: 150px;
  }

  &__btn {
    display: inline-block;
    width: 100%;
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 36px;
    text-transform: uppercase;
    color: var(--accent-color);
    margin-bottom: 55px;
  }

  &__content {
    border-radius: 22px;
    background-color: var(--blue-color);
    padding: 35px 58px;
  }

  &__price {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    color: #757575;
    & span {
      font-weight: 500;
      color: #393939;
    }
  }

  &__order {
    padding: 21px 47px;
    background-color: var(--light-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 16px;
    margin-bottom: 56px;

    &--num {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 24px;
      color: #757575;
      & span {
        font-weight: 500;
        color: #393939;
      }
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__info-block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 150px;
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;

    color: #fff;

    & div {
      margin-bottom: 25px;
    }
  }

  &__payment-block {
    padding-left: 100px;
  }

  &__payment-itog {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;
    color: #fff;
    margin-bottom: 15px;
  }

  &__payment-price {
    padding: 5px 40px;
    background-color: var(--light-color);
    text-align: center;
    margin-bottom: 25px;
    & span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 32px;

      color: #e34646;
    }
  }
  &__payment-texts {
    margin-bottom: 38px;
  }
  &__payment-text {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    color: #e3e3e3;
    margin-bottom: 4px;
    & span {
      font-weight: 500;
      color: #fff;
    }
  }

  &__politic {
    margin: 0;
    margin-top: 20px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    color: #bababa;

    & span {
      color: var(--light-color);
    }
  }
}
