.step5 {
  background-color: var(--dark-color);
  padding-top: 70px;
  padding-bottom: 184px;
  position: relative;

  &__cosmos {
    position: absolute;
    width: 350px;
    height: 300px;
    top: -50px;
    right: 50%;
    margin-right: -175px;
  }
  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 36px;
    text-transform: uppercase;
    color: var(--accent-color);
    margin-bottom: 55px;
  }

  &__title-block {
    display: grid;
    grid-template-columns: 340px;
  }

  &__title-block-h3 {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;

    text-transform: uppercase;
    color: #fff;
    margin-bottom: 37px;
  }

  &__content {
    border-radius: 22px;
    background-color: var(--blue-color);
    padding: 35px 58px;
  }

  &__order {
    padding: 21px 47px;
    background-color: var(--light-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 16px;
    margin-bottom: 56px;

    &--text {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 24px;
      color: #393939;
    }
  }

  &__list-bank {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;

    &--item1 {
      position: relative;
      border-radius: 22px;
      border: 1px solid var(--light-color);
      padding: 17px 30px 82px;

      &-title {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 24px;
        text-transform: uppercase;
        color: #fff;
        margin-bottom: 24px;
      }
    }
  }
  &__param--block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
  }

  &__param--name {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    color: #e3e3e3;
  }

  &__param--price {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    color: #fff;
  }

  &__btn {
    position: absolute;
    min-width: 35%;
    left: 50%;
    bottom: -7%;
    transform: translateX(-50%);
  }

  &__list-bank--item2 {
    border-radius: 22px;
    backdrop-filter: blur(8px);
    background-color: rgba(255, 255, 255, 0.23);
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;

    text-transform: uppercase;
    color: #d6d6d6;
  }
}
