.choices {
  padding: 20px 20px;
  border-radius: 22px;
  width: 100%;
  height: 76px;
  text-align: center;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 24px;
  color: var(--dark-color);

  &--focus {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
}
