.footer {
  background-color: var(--dark-color);

  &__container {
    padding-top: 100px;
    padding-bottom: 43px;
  }

  &__block {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
  }

  &__phone {
    font-family: var(--third-family);
    font-weight: 400;
    font-size: 32px;

    text-align: center;
    color: #fefefe;
  }

  &__social {
    display: flex;
    align-items: center;

    & a:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__copyright {
    font-family: var(--third-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 580%;
    text-align: center;
    color: #757575;
  }
}
