.form {
  display: flex;
  flex-direction: column;
  width: 100%;
  &__label {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 8px;

    width: 100%;
    margin-bottom: 8px;
    &:last-child {
      margin-bottom: 21px;
    }
  }
  .just-validate-error-label {
    padding-left: 20px;
  }

  &__block {
    margin-bottom: 100px;
  }
  &__input {
    padding: 20px 20px;
    border-radius: 22px;
    width: 100%;
    height: 76px;
    text-align: center;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    color: var(--dark-color);
    &::placeholder {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 24px;
      text-align: center;
      color: #757575;
    }
  }

  &__telegram-name {
    display: none;
  }
}
