.work {
  background-color: var(--dark-color);
  padding-top: 100px;
  padding-bottom: 80px;

  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 48px;
    color: var(--accent-color);
    margin-bottom: 14px;
  }
  &__descr {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    text-transform: lowercase;
    color: var(--light-color);
  }

  &__swiper {
    width: 100%;
    height: 350px;
    padding-bottom: 35px;
  }

  &__item {
    min-height: 160px;
    border-radius: 22px;
    padding: 60px;
    background-color: var(--light-color);
    display: flex;
    align-items: center;

    &--number {
      border-radius: 12px;
      padding: 11px 21px;
      font-family: var(--second-family);
      font-weight: 500;
      font-size: 48px;
      background-color: var(--blue-color);
      color: var(--light-color);
      margin-right: 18px;
    }

    &--text {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 20px;
      color: var(--dark-color);
    }
  }
}
