.partners {
  background-color: #f5f5f5;
  padding-top: 33px;
  padding-bottom: 55px;

  &__container {
    padding: 54px 100px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;
    border-radius: 22px;
    background-color: var(--light-color);
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 40px;
    text-transform: uppercase;
    color: var(--dark-color);
    margin-bottom: 8px;
  }

  &__descr {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;
    color: var(--dark-color);
  }

  &__item {
    width: 320px;
    border-radius: 22px;
    display: flex;
    align-items: center;
    background-color: #f5f5f5;
    padding: 25px 33px;

    &--blue {
      background-color: var(--blue-color);

      & .partners__item--img {
        background-color: var(--light-color);
      }

      & span {
        color: var(--light-color);
      }
    }

    &--img {
      border-radius: 12px;
      padding: 11px;
      background-color: var(--blue-color);
      margin-right: 14px;
    }
  }
}
