.order {
  background-color: #f5f4f4;

  &__container {
    padding: 93px 75px;
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 32px;
    text-transform: uppercase;
    color: #393939;
    text-align: center;
    margin-bottom: 43px;

    & span {
      color: var(--accent-color);
    }
  }
  &__form {
    display: flex;
    flex-direction: column;
    border-radius: 22px;
    background-color: var(--blue-color);
    padding: 28px 67px;
  }

  &__subtitle {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;
    text-transform: uppercase;
    color: #fff;
    margin-bottom: 41px;
    text-align: center;
  }
}
