.hero {
  background-color: var(--dark-color);
  padding-top: 70px;
  padding-bottom: 40px;

  &__blocks {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  &__block-left {
    display: flex;
    flex-direction: column;
    padding-right: 57px;
    padding-left: 57px;
    padding-bottom: 109px;
  }

  &__logo {
    margin-bottom: 32px;
  }
  &__subtitle {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 32px;
    color: var(--accent-color);
    margin-bottom: 10px;
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 40px;
    text-transform: uppercase;
    color: var(--light-color);
    margin-bottom: 15px;

    &-info {
      margin: 0;
      font-family: var(--second-family);
      font-weight: 500;
      font-size: 32px;
      text-transform: uppercase;
      color: var(--light-color);
      margin-bottom: 35px;
    }
  }
  &__descr {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    color: var(--light-color);
    margin-bottom: 67px;
  }

  &__btns {
    display: flex;
    justify-content: space-between;
  }

  &__block-right {
    background-image: url("../img/hero_men.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 109px 62px;
    display: flex;
    flex-direction: column-reverse;
    align-items: end;
  }
}
