.step3 {
  background-color: var(--dark-color);
  padding-top: 70px;
  padding-bottom: 184px;
  position: relative;

  &__img {
    position: absolute;
    bottom: 115px;
    left: 50px;
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 36px;
    text-transform: uppercase;
    color: var(--accent-color);
    margin-bottom: 55px;
  }

  &__content {
    border-radius: 22px;
    background-color: var(--blue-color);
    padding: 35px 58px;
  }

  &__order {
    padding: 21px 47px;
    background-color: var(--light-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 16px;
    margin-bottom: 56px;

    &--num {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 24px;
      color: #757575;
      & span {
        font-weight: 500;
        color: #393939;
      }
    }
  }

  &__price {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    color: #757575;
    & span {
      font-weight: 500;
      color: #393939;
    }
  }

  &__personal-block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 300px;
  }

  &__personal--title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;

    text-transform: uppercase;
    color: #fff;
    margin: 0;
    margin-bottom: 19px;
  }
  &__name-input {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;

    color: #e3e3e3;
    margin-bottom: 8px;
  }

  &__phone,
  &__passport-num {
    border-radius: 16px;
    background-color: var(--light-color);
    padding: 12px 17px;
    margin-bottom: 30px;

    & p {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 24px;
      text-transform: lowercase;
      color: var(--dark-color);

      & span {
        color: var(--blue-color);
      }
    }
  }

  &__passport-num {
    border-radius: 16px;
    background-color: var(--light-color);
    padding: 12px 17px;

    & p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 20px;

      color: #757575;
    }
  }

  &__personal-fio {
    display: flex;
    flex-direction: column;
    align-items: center;

    &--input {
      width: 100%;
      border-radius: 16px;
      border: none;
      background-color: var(--light-color);
      padding: 12px 17px;
      margin-bottom: 13px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 20px;
      color: #757575;
    }
  }
  &__personal-fio-block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    align-items: center;

    &--radio {
      display: flex;
      flex-direction: column;
    }
  }
  &__radio {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    color: #e3e3e3;
  }
}
